import React, { useState } from 'react';

import { makeStyles } from '@material-ui/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import HomeIcon from '@material-ui/icons/Home';
import SettingsIcon from '@material-ui/icons/Settings';
import SearchIcon from '@material-ui/icons/Search';
import AddIcon from '@material-ui/icons/Add';
import Grid from '@material-ui/core/Grid';
import Education from './education'
import Enicarthage from './Logo_ENICarthage.jpg';
import IPEIM from './IPEIM.jpg';
import sagemcom from './sagemcom.JPG';
import ECT from './ECT.jpg'
const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    //backgroundColor: theme.palette.background.paper
  },
  tabContent: {
   
  }

}));


export default function Experience() {
  const classes = useStyles();
  const [tabs, setTabs] = useState([
    {
      active: true,
      label: 'Home',
      content: <Education titre="Projet de Fin d’Étude." image={sagemcom} contenue="Conception d’une machine In-Situ pour assurer le test hors tension et
      sous tension des cartes électroniques :- Interface homme-machine : création des interfaces pour entrer les
      donnés utilisateurs, paramètres de configuration tels que le port de
      communication série et pour l’affichage des résultats de deux tests.
      Logiciel utilisé : LabVIEW.
      - Programmation : création d’une tâche principale qui exécute infiniment
      et qui donne l’autorisation d’exécution aux autres tâches
      secondaires, tout est par l’intermédiaire des sémaphores binaires.
      Matériels : STM32 sous FreeRTOS, implémentation C.
      - CAO : conception de le gabarit du système qui comprend les différents
      actionneurs et les systèmes de guidages.
      Logiciels utilisés :Solidworks + KeyShot.Ingénieur Mécatronique spécialisé en systèmes industriels à L’École
      Nationale d’Ingénieurs de Carthage." university="Sagemcom" date="" certification="certificat"></Education>,
      icon: <HomeIcon />
    },
    {
      active: false,
      label: 'Settings',
      content: <Education titre="Stage de fin d’année." image={ECT} contenue="Conception d’une ligne d’assemblage sur le logiciel CATIA." university="ElectroContact" date="" ></Education>,
      icon: <SettingsIcon />
    },
    {
      active: false,
      label: 'Settings',
      content: <Education titre="Stage Ouvrier." image={ECT} contenue=" Observation de la production et faire des opérations simples." university="ElectroContact" date=""certification="diplome" ></Education>,
      icon: <SettingsIcon />
    },
    {
      active: false,
      disabled: true,
      hidden:true,
      label: 'Search',
      content: 'Search Content',
      icon: <SearchIcon />
    },
    {
      active: false,
      hidden: true,
      label: 'Add',
      content: 'AddContent',
      icon: <AddIcon />
    }
  ]);

  const onChange = (e, value) => {
    setTabs(
      tabs
        .map(tab => ({ ...tab, active: false }))
        .map((tab, index) => ({
          ...tab,
          active: index === value
        }))
    );
  };

  const active = tabs.findIndex(tab => tab.active);
  const content = tabs[active].content;
   
  return (
    <div className={classes.root}>
      <Grid container spacing={4}>
        <Grid item xs='3'>
          <Tabs orientation='vertical' value={active} onChange={onChange}>
            {tabs
              .filter(tab => !tab.hidden)
              .map(tab => (
                <Tab
                  key={tab.label}
                  disabled={tab.disabled}
                  icon={tab.icon}
                  label={tab.label}
                />
              ))}
          </Tabs>
        </Grid>
        <Grid item xs='9'>
          <Typography component="div" className={classes.tabContent}>
            {content}
          </Typography>
        </Grid>
      </Grid>
    </div>
  );
}
