import React, { useState, useEffect } from 'react';
import Box from '@material-ui/core/Box';

import './sty.css';
import mypic from './my_pic.JPG'
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import CalendarTodayTwoToneIcon from '@material-ui/icons/CalendarTodayTwoTone';
import AddLocationIcon from '@material-ui/icons/AddLocation';


import Experience from './expérience'
import AddIcCall from '@material-ui/icons/AddIcCall';
import EmailIcon from '@material-ui/icons/Email';
import Compet from '../competences/competences';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import RenderingTabsBasedOnState from './Education_main'
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Certs from './Certs'
const styles = theme => ({
    root: {
        flexGrow: 1
    },
    paper: {
        padding: theme.spacing(1),
        textAlign: 'center',
        color: theme.palette.text.secondary,



    },
    spacing: 8,



});
const mystyle = {
    bg_paper: {
        backgroundColor: "#FFF3E0"
    },
    bg_paper_L: {
        backgroundColor: "#FFF3E0"
    }

}


const MainComponent = withStyles(styles)(({ classes }) => (
    <div className={classes.root}>

        <Grid container alignItems="stretch" spacing={1} >

            <Grid item xs={12} sm={4}>
                <Paper style={mystyle.bg_paper_L} className={classes.paper}>
                   
                        <Paper elevation={12} variant="elevation">
                            <img className="my_pic " src={mypic}></img>
                        </Paper>
                   <Paper>
                       <Box boxShadow={3}>
                    <h3>Mohamed BELKAHLA</h3></Box>
                    <p>Ingénieur mécatronique énicarthage</p>
                    <Grid container spacing={1}>
                        <Grid item xs={2}>

                            <CalendarTodayTwoToneIcon></CalendarTodayTwoToneIcon>
                        </Grid>
                        <Grid item xs="auto">
                            <p>7 janvier 1996</p>
                        </Grid>
                    </Grid>
                    <Grid container spacing={1}>
                        <Grid item xs={2}>
                            <AddLocationIcon></AddLocationIcon>
                        </Grid>
                        <Grid item xs="auto">
                            <p>Rue Jabel Tarek,Teboulba, Monastir,Tunisie</p>
                        </Grid>
                    </Grid>
                    <Grid container spacing={1}>
                        <Grid item xs={2}>
                            <AddIcCall></AddIcCall>
                        </Grid>
                        <Grid item xs="auto">
                            <p>28 666 450</p>
                        </Grid>
                    </Grid>
                    <Grid container spacing={1}>
                        <Grid item xs={2}>
                            <EmailIcon></EmailIcon>
                        </Grid>
                        <Grid item xs="auto">
                            <p>mohamed.belkahla19@gmail.com</p>
                        </Grid>
                    </Grid>

                    </Paper>



                    <h2 >Compétences</h2><hr ></hr>
                    <Compet></Compet>

                </Paper>
            </Grid>
            <Grid item xs={12} sm={8} >{console.log(classes)}
                <Paper style={mystyle.bg_paper} className={classes.paper}>

                    <Box m={1} boxShadow={"0 18px 28px #FFD600, 0 10px 10px #FFD600;"}>
                        <Paper elevation={4} className={classes.paper}>


                            <Typography color="#b00b69" variant="h4">Education</Typography>
                            <hr></hr>
                            <RenderingTabsBasedOnState></RenderingTabsBasedOnState>


                        </Paper>
                    </Box>



                    <Box m={1} boxShadow={3}>
                        <Paper elevation={24} className={classes.paper}>

                            <hr></hr>
                            <Typography color="#b00b69" variant="h4">Experiences</Typography>
                          <Experience></Experience>

                        </Paper>
                    </Box>
                    <Box m={1} boxShadow={3} >
                        <Paper elevation={4} className={classes.paper}>

                            <hr></hr>
                            <Typography color="#b00b69" variant="h4">certificats</Typography>
                            <Certs></Certs>
                            <hr></hr>

                        </Paper>
                    </Box>
                </Paper>
            </Grid>

        </Grid>
    </div>
));

export default MainComponent;