import React, { useState } from 'react';

import { makeStyles } from '@material-ui/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Typography from '@material-ui/core/Typography';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Collapse from '@material-ui/core/Collapse';

import PersonIcon from '@material-ui/icons/Person';
import ContactMailIcon from '@material-ui/icons/ContactMail';
import ContactPhoneIcon from '@material-ui/icons/ContactPhone';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { PinDropSharp } from '@material-ui/icons';
import { Paper } from '@material-ui/core';
import { createMuiTheme } from '@material-ui/core/styles';
import { purple } from '@material-ui/core/colors';
import Box from "@material-ui/core/Box"


const useStyles = makeStyles(theme => ({
  card: {
    maxWidth: "100%"
  },
  expand: {
    marginLeft: 'auto'
  },
  paper_clor: {
    backgroundColor: "#b00b69",
    maxWidth: "100%"

  },
  spacing: 8

}));


const ExpandIcon = ({ expanded }) =>
  expanded ? <ExpandLessIcon /> : <ExpandMoreIcon />;

export default function Education(props) {

  const classes = useStyles();
  const [expanded, setExpanded] = useState(false);

  const toggleExpanded = (props) => {
    if (!props.certification)
      setExpanded(!expanded);
  };

  return (
    <Card className={classes.card}>
      <CardHeader
        title={props.titre}
        subheader={props.university}
        avatar={
          <Avatar alt="University" src={props.image || "tt"} className={classes.small + " " + "inline_block"} />
        }
      />
      <CardContent>
        <Typography variant="caption">{props.date}</Typography>
        <Typography>
          {props.contenue}
        </Typography>
      </CardContent>
      {props.certification ?
        <CardActions disableActionSpacing>


          <IconButton
            className={classes.expand}
            onClick={toggleExpanded}
          >
            <ExpandIcon expanded={expanded} />
          </IconButton>
        </CardActions>
        : null}
      <Collapse in={expanded}>
        <CardContent>
          {props.certification ?
            <Typography>

              <Paper elevation={24}className={classes.paper_clor} >
                <Box  py={2}>

                  <img style={{ maxWidth: "95%" }} src={props.certification}></img>
                </Box>
              </Paper>

            </Typography> : null}
        </CardContent>
      </Collapse>
    </Card>
  );
}
