import React, { useState } from 'react';

import { makeStyles } from '@material-ui/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import HomeIcon from '@material-ui/icons/Home';
import SettingsIcon from '@material-ui/icons/Settings';
import SearchIcon from '@material-ui/icons/Search';
import AddIcon from '@material-ui/icons/Add';
import Grid from '@material-ui/core/Grid';
import Education from './education'
import Enicarthage from './Logo_ENICarthage.jpg';
import IPEIM from './IPEIM.jpg';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    //backgroundColor: theme.palette.background.paper
  },
  tabContent: {
    //padding: theme.spacing(2)
  }
}));


export default function RenderingTabsBasedOnState() {
  const classes = useStyles();
  const [tabs, setTabs] = useState([
    {
      active: true,
      label: 'Home',
      content: <Education titre="Obtention diplôme National d’ingénieur." image={Enicarthage} contenue="Ingénieur Mécatronique spécialisé en systèmes industriels à L’École
      Nationale d’Ingénieurs de Carthage." university="ENicarthage" date="" certification="certificat"></Education>,
      icon: <HomeIcon />
    },
    {
      active: false,
      label: 'Settings',
      content: <Education titre="Réussite au Concours National d’entrée aux écoles d'ingénieurs" image={IPEIM} contenue=" Institut préparatoire aux études d’ingénieurs de Monastir." university="IPEIM" date="" ></Education>,
      icon: <SettingsIcon />
    },
    {
      active: false,
      label: 'Settings',
      content: <Education titre="Réussite au Baccalauréat avec la mention très bien" image="" contenue=" Bac Technologique." university="Lycée Téboulba" date=""certification="diplome" ></Education>,
      icon: <SettingsIcon />
    },
    {
      active: false,
      disabled: true,
      hidden:true,
      label: 'Search',
      content: 'Search Content',
      icon: <SearchIcon />
    },
    {
      active: false,
      hidden: true,
      label: 'Add',
      content: 'AddContent',
      icon: <AddIcon />
    }
  ]);

  const onChange = (e, value) => {
    setTabs(
      tabs
        .map(tab => ({ ...tab, active: false }))
        .map((tab, index) => ({
          ...tab,
          active: index === value
        }))
    );
  };

  const active = tabs.findIndex(tab => tab.active);
  const content = tabs[active].content;
   
  return (
    <div className={classes.root}>
      <Grid container spacing={4}>
        <Grid item xs='3'>
          <Tabs orientation='vertical' value={active} onChange={onChange}>
            {tabs
              .filter(tab => !tab.hidden)
              .map(tab => (
                <Tab
                  key={tab.label}
                  disabled={tab.disabled}
                  icon={tab.icon}
                  label={tab.label}
                />
              ))}
          </Tabs>
        </Grid>
        <Grid item xs='9'>
          <Typography component="div" className={classes.tabContent}>
            {content}
          </Typography>
        </Grid>
      </Grid>
    </div>
  );
}
